import axios from 'axios';

//   URL  API
//    localhost:8000,  production -  origin
const baseURL = process.env.NODE_ENV === 'development' 
  ? 'http://localhost:8000' 
  : window.location.origin;

//   URL   
axios.defaults.baseURL = baseURL;
axios.defaults.withCredentials = true; //   credentials

//     API
export const API_ERROR_EVENT = 'api-connection-error';

//       '
export const emitConnectionError = () => {
  const event = new CustomEvent(API_ERROR_EVENT, { 
    detail: { message: ' \'  :   .' } 
  });
  window.dispatchEvent(event);
};

//       
axios.interceptors.request.use(
  config => {
    console.log(`[API Request] ${config.method?.toUpperCase()} ${config.url}`);
    
    //     
    if (process.env.NODE_ENV === 'development') {
      console.log('Request Config:', config);
    }
    return config;
  },
  error => {
    console.error('[API Request Error]', error);
    return Promise.reject(error);
  }
);

axios.interceptors.response.use(
  response => {
    console.log(`[API Response] ${response.status} from ${response.config.url}`);
    
    //     
    if (process.env.NODE_ENV === 'development') {
      console.log('Response Data:', response.data);
    }
    return response;
  },
  error => {
    //    -   
    if (!error.response) {
      console.error('[API Network Error]   \'  ');
      emitConnectionError();
    } else {
      console.error('[API Response Error]', error.response.status, error.response.data);
    }
    return Promise.reject(error);
  }
);

export default axios; 